import {createFilmCardTemplate} from './view/film-card-view';
import {createFilmsContainerTemplate} from './view/films-container-view';
import {createLoadMoreBtnTemplate} from './view/load-more-btn-view';
import {createMainNavigationTemplate} from './view/main-navigation-view';
import {createProfileRatingTemplate} from './view/profile-rating-view';
import {createSortTemplate} from './view/sort-list-view';
import {filmsCardsMok} from "./mock/card";
import {filmsFiltersMok} from "./mock/main-navigation";
import {render} from "./render";

const SHOWING_FILMS_COUNT_ON_START = 5;
const SHOWING_FILMS_COUNT_BY_BUTTON = 5;

const renderF = (container, template, place = `beforeend`) => {
  container.insertAdjacentHTML(place, template);
};

const header = document.querySelector(`.header`);
const main = document.querySelector(`.main`);

renderF(header, createProfileRatingTemplate());

const filters = filmsFiltersMok();

renderF(main, createMainNavigationTemplate(filters));
renderF(main, createSortTemplate());
renderF(main, createFilmsContainerTemplate());

const filmsContainer = document.querySelector(`.films-list__container`);

const films = filmsCardsMok();

// renderF(main, createLoadMoreBtnTemplate());

render()
let showingFilmsCount = SHOWING_FILMS_COUNT_ON_START;
films.slice(0, showingFilmsCount)
  .forEach((film) => renderF(filmsContainer, createFilmCardTemplate(film)));

const showMoreButton = document.querySelector(`.films-list__show-more`);

showMoreButton.addEventListener(`click`, () => {
  const prevFilmsCount = showingFilmsCount;
  showingFilmsCount = showingFilmsCount + SHOWING_FILMS_COUNT_BY_BUTTON;

  films.slice(prevFilmsCount, showingFilmsCount)
    .forEach((film) => renderF(filmsContainer, createFilmCardTemplate(film)));
  if (showingFilmsCount >= films.length) {
    showMoreButton.remove();
  }
});

